<template>
<div class="tabs-wrap">
  <el-tabs class="tabs" @click.right.prevent="openTabMenu" v-model="editableTabsValue" type="card" @tab-click="clickTab" @tab-remove="removeTab">
    <el-tab-pane
        v-for="item in editableTabs"
        :key="item.name"
        :label="item.title"
        :name="item.name"
        :closable="item.closable"
    >
    </el-tab-pane>
  </el-tabs>
  <ul v-show="menuShow" :style="{left: menuOffsetLeft + 'px', top: menuOffsetTop + 'px'}" class="contextmenu">
    <li @click="refreshSelectedTab(selectedTabName)">
      刷新
    </li>
    <li v-if="selectedTabName!=='Home'" @click="closeSelectedTab(selectedTabName)">
      关闭
    </li>
    <li @click="closeOtherTab(selectedTabName)">
      关闭其他
    </li>
    <li v-if="selectedTabName!=='Home'" @click="closeAllTab(selectedTabName)">
      关闭全部
    </li>
  </ul>
</div>
</template>


<script>
import router from "@/router";

export default {
  name: 'Tabs',
  props: [
    'router',
    'currentRoute',
    'getRoute'
  ],
  data() {
    return {
      // eslint-disable-next-line vue/no-dupe-keys
      currentRoute: this.$props.currentRoute,
      selectedTabName: '',
      menuShow: false,
      menuOffsetLeft: 500,
      menuOffsetTop: 500,
      mouseOffsetX: 0,
      mouseOffsetY: 0,
      editableTabsValue: this.$props.getRoute('Home').name,
      editableTabs: [{
        title: this.$props.getRoute('Home').meta.title,
        name: 'Home',
      }]
    }
  },
  mounted() {
    document.querySelector('.tabs').addEventListener('mousemove',e=>{
      this.mouseOffsetX = e.clientX-document.querySelector('.el-aside').scrollWidth;
      this.mouseOffsetY = e.offsetY;
    });
  },
  beforeUpdate() {
    // eslint-disable-next-line vue/no-mutating-props
    this.currentRoute = this.$props.currentRoute;
  },
  methods: {
    addTab(targetName) {
      let hasTab = false;
      for (const tab of this.editableTabs) {
        if(tab.name === targetName){
          hasTab = true;
        }
      }
      if(!hasTab){
        this.editableTabs.push({
          title: this.$props.getRoute(targetName).meta.title,
          name: targetName,
          closable: true
        });
      }
      this.editableTabsValue = targetName;
      this.$props.router.push({name:targetName});
    },
    clickTab(tab){
      this.$props.router.push({name:tab.props.name});
    },
    removeTab(targetName) {
      let tabs = this.editableTabs;
      let activeName = this.editableTabsValue;
      if (activeName === targetName) {
        tabs.forEach((tab, index) => {
          if (tab.name === targetName) {
            let nextTab = tabs[index + 1] || tabs[index - 1];
            if (nextTab) {
              activeName = nextTab.name;
              this.$props.router.push({name:nextTab.name});
            }
          }
        });
      }
      this.editableTabsValue = activeName;
      this.editableTabs = tabs.filter(tab => tab.name !== targetName);
    },
    openTabMenu(e){
      if(e.target.classList.contains("el-tabs__item")){
        this.menuOffsetLeft = this.mouseOffsetX;
        this.menuOffsetTop = this.mouseOffsetY;
        this.selectedTabName = e.target.id.replace('tab-','');
        this.menuShow = true;
      }
    },
    async refreshSelectedTab(selectedTabName){
      await this.$props.router.replace({name: '404'});
      this.$props.router.replace({name: selectedTabName});
      this.menuShow = false;
    },
    closeSelectedTab(selectedTabName){
      for (const tab of this.editableTabs) {
        if(tab.name !== 'Home' && tab.name === selectedTabName){
          this.removeTab(selectedTabName);
        }
      }
      this.menuShow = false;
    },
    closeOtherTab(selectedTabName){
      for (const tab of this.editableTabs) {
        if(tab.name !== 'Home' && tab.name !== selectedTabName){
          this.removeTab(tab.name);
        }
      }
      this.menuShow = false;
    },
    closeAllTab(selectedTabName){
      for (const tab of this.editableTabs) {
        if(tab.name !== 'Home'){
          this.removeTab(tab.name);
        }
      }
      this.menuShow = false;
    }
  },
  watch: {
    currentRoute(nv, ov){
      if(nv.meta.asideNav){
        this.addTab(nv.name);
      }
    },
    menuShow(){
      if (this.menuShow) {
        document.body.addEventListener('click', ()=>this.menuShow=false)
      } else {
        document.body.removeEventListener('click', ()=>this.menuShow=false)
      }
    }
  }
}
</script>

<style lang="scss">
.tabs-wrap{
  position: relative;
  box-shadow: 0px 1px 1px 1px rgba(128, 128, 128, 0.2);
  .el-tabs__header{
    margin-bottom: 0 !important;
  }
  .el-tabs__item{
    border-bottom: initial !important;
    height: 1.8rem;
    line-height: 1.8rem;
    font-size: 12px;
    &.is-active{
      background-color: #324d6a;
      color: white;
    }
  }
  .el-tabs__nav-prev,.el-tabs__nav-next{
    height: 1.8rem;
    line-height: 1.8rem;
  }
  .contextmenu {
    margin: 0;
    background: #fff;
    z-index: 100;
    position: absolute;
    list-style-type: none;
    padding: 5px 0;
    border-radius: 4px;
    font-size: 12px;
    font-weight: 400;
    color: #333;
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, .3);
    li {
      margin: 0;
      padding: 7px 16px;
      cursor: pointer;
      &:hover {
        background: #eee;
      }
    }
  }
}
</style>